import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
    name: 'sideMenuDoc',
    pure: false
})
export class sideMenuTextPipe implements PipeTransform{
    transform(value: string): string{
        if(value.length > 25){
            var text = value.substring(0, 15) + "..." + value.substring(value.length-8, value.length);
            return text;
        }else{
            return value;
        }

    }
}
